import React, { useEffect, useState } from "react"
import CustomLink from "./CustomLink"

import Navbar from "react-bootstrap/Navbar"
import Nav from "react-bootstrap/Nav"

const styles = {
  navbar: {
    backgroundColor: "#7D3C98",
  },
  center: { display: "flex", justifyContent: "center", alignItems: "center" },
}

export default function NavMenu(props) {
  const [atBottom, setAtBottom] = useState(false)
  const atBottomProp = props.atBottom
  const navbarHeight = props.navbarHeight
  useEffect(() => {
    setAtBottom(atBottomProp)
  }, [atBottomProp])

  return (
    <Navbar
      style={{ ...styles.navbar }}
      variant="dark"
      expand="sm"
      sticky="top"
    >
      <Navbar.Brand href="/">
        <strong style={{ fontWeight: 700 }}>Winton Nathan-Roberts</strong>
      </Navbar.Brand>
      <Navbar.Toggle aria-controls="basic-navbar-nav" />
      <Navbar.Collapse id="basic-navbar-nav">
        <Nav className="ml-auto" variant="pills">
          <CustomLink
            activeClass={atBottom ? "x" : "active"}
            to="aboutme"
            spy={true}
            smooth={true}
            offset={-navbarHeight-20}
            duration={500}
          >
            <strong style={{ fontWeight: 700, color: "white" }}>ABOUT</strong>
          </CustomLink>
          <CustomLink
            activeClass={atBottom ? "x" : "active"}
            to="portfolio"
            spy={true}
            smooth={true}
            offset={-navbarHeight}
            duration={500}
          >
            <strong style={{ fontWeight: 700, color: "white" }}>
              PORTFOLIO
            </strong>
          </CustomLink>
          <CustomLink
            activeClass="active"
            to="contact"
            spy={true}
            smooth={true}
            offset={-navbarHeight}
            active={atBottom}
            duration={500}
          >
            <strong style={{ fontWeight: 700, color: "white" }}>CONTACT</strong>
          </CustomLink>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  )
}
